.boton {
  border: none;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  text-decoration: none;
  font-family: inherit;
}

.boton-medio {
  padding: 10px 16px;
  font-size: 0.9rem;
  min-height: 40px;
}

@media (max-width: 768px) {
  .boton-medio {
    padding: 8px 14px;
    font-size: 0.85rem;
    min-height: 36px;
  }
}


.boton.boton-medio.boton-ver-detalle {
  background: linear-gradient(135deg, #6c757d 0%, #495057 100%);
  color: white;
  padding: 12px 20px;
  min-height: 44px;
  border-radius: 50px;
  font-size: 1.25rem;
}

.boton.boton-medio.boton-ver-detalle:hover {
  background: linear-gradient(135deg, #495057 0%, #343a40 100%);
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(108, 117, 125, 0.3);
}

.boton.boton-medio.boton-agregar-carrito {
  background: linear-gradient(135deg, #a8a4e6 0%, #8b7fd8 100%);
  color: white;
  box-shadow: 0 4px 12px rgba(168, 164, 230, 0.3);
}

.boton.boton-medio.boton-agregar-carrito:hover {
  background: linear-gradient(135deg, #8b7fd8 0%, #7c6fd1 100%);
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(168, 164, 230, 0.5);
}

.boton-enviar {
  background: linear-gradient(135deg, #f59a16 0%, #e67e22 100%);
  color: white;
  box-shadow: 0 4px 12px rgba(245, 154, 22, 0.3);
  width: 50%;
  font-weight: bold;
}

.boton-enviar:hover {
  background: linear-gradient(135deg, #e67e22 0%, #d35400 100%);
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(245, 154, 22, 0.5);
}

.boton.disabled {
  background: #6c757d !important;
  color: #adb5bd !important;
  cursor: not-allowed !important;
  transform: none !important;
  box-shadow: none !important;
  opacity: 0.6;
}

.boton.disabled:hover {
  background: #6c757d !important;
  color: #adb5bd !important;
  transform: none !important;
  box-shadow: none !important;
}


.modo-nocturno .boton.boton-medio.boton-agregar-carrito {
  background: linear-gradient(135deg, #a8a4e6 0%, #8b7fd8 100%);
  color: white;
  box-shadow: 0 4px 12px rgba(168, 164, 230, 0.3);
}

.modo-nocturno .boton.boton-medio.boton-agregar-carrito:hover {
  background: linear-gradient(135deg, #8b7fd8 0%, #7c6fd1 100%);
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(168, 164, 230, 0.5);
}
